

.buttons{
    margin: 5px;
    padding: 15px;
    border: none;
    background-color: rgba(201, 255, 4, 0.411);
    font-size: 25px;
    font-weight: 900;
    color: white;
    width: 100%;
}
.buttons:hover{
    cursor: pointer;
    background-color: rgba(4, 92, 255, 0.589);
}
.buttonClear{
    background-color: red;
}
.buttonEnter{
    background-color: green;
}
.cont__buttons{
    margin-bottom: 30px;
    width: 100%;
}
.buttons__columns{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}