body{
  background-image: url(../imgs/fondo-banner-trazo-pincel-rojo-negro-perfecto-canva.jpg);
  background-position: center;
  background-size: cover;
}

.content{
  height: 100vb;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content__cont{
  width: 350px;
  height: 500px;
  border-radius: 5px;
  background: linear-gradient(to bottom left, #720000b9 25%,#0000ff9f 50%);

}
.cont__cont{
  padding: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}